import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';

import { HttpClient } from '@angular/common/http';
import { formatCurrency } from '@angular/common';

@Component({
  selector: 'block-component',
  templateUrl: './blockList.component.html',
  styleUrls: ['./blockList.component.css'],
})
export class BlockListComponent {
  public blockguest: Guest[];
  show = false;

  inplus() {
    this.show = true;
  }

  constructor(public http: HttpClient) {}

  ngOnInit() {
    this.http
      .get<any>('http://localhost:3000/guest/blocked')
      .subscribe((data) => {
        this.blockguest = data.map((guest) => {
          guest.starttime = new Date(guest.starttime * 1000).toLocaleDateString(
            'en-US'
          );
          return <Guest>guest;
        });
      });
  }

  add(blockemail: NgForm) {
    console.log(blockemail.value.email);
    var email = blockemail.value.email;
    var bemail = blockemail.value.bemail;
    console.log(bemail);
    this.http
      .put<any>('http://localhost:3000/guest/block', {
        email: email,
        bemail: bemail,
      })
      .subscribe((data) => {
        console.log(data);
        return this.ngOnInit();
      });

    blockemail.reset();

    this.show = false;
  }

  unblock(bemail: string) {
    console.log(bemail);
    this.http
      .put<any>('http://localhost:3000/guest/unblock', { email: bemail })
      .subscribe((data) => {
        console.log(data);
        return this.ngOnInit();
      });
  }
  cancel() {
    this.show = false;
  }
}

export interface Guest {
  name?: string;
  email?: string;
  phone?: string;
  organizername?: string;
  organizermail?: string;
  organizerphone?: string;
  organization?: string;
  starttime?: string;
  endtime?: string;
  checkin?: string;
  checkout?: string;
  status?: string;
  blocker?: string;
}
