import { Component, OnInit } from '@angular/core';

import { HttpClient } from '@angular/common/http';

import { NgForm } from '@angular/forms';

@Component({
  selector: 'guest-component',
  templateUrl: './adminManage.component.html',
  styleUrls: ['./adminManage.component.css'],
})
export class AdminManageComponent {
  public musers: Auser[];
  public staticUser: Auser[];

  email: any;
  p: number = 1;

  constructor(public http: HttpClient) {}
  ngOnInit() {
    this.http
      .get<any>('http://localhost:3000/admin/users')
      .subscribe((data) => {
        this.musers = data.map((user) => {
          return <Auser>user;
        });
        this.staticUser = this.musers;
        console.log(this.musers);
      });
  }

  udelete(user) {
    this.musers.splice(this.musers.indexOf(user));
    this.http
      .post<any>('http://localhost:3000/admin/delete', {
        username: user.username,
      })
      .subscribe((data) => {
        console.log(data);
        return this.ngOnInit();
      });
  }
}

export interface Auser {
  firstName?: string;
  lastName?: string;
  username?: string;
  phonenumber?: string;
  password?: string;
}
