import { CommonModule } from '@angular/common';
import { CUSTOM_ELEMENTS_SCHEMA, NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule, Route } from '@angular/router';
import { HttpClientModule } from '@angular/common/http';
import { AdminComponent } from './checkin-admin.component';
import { BlockListComponent } from './block-list/blockList.component';
import { GuestListComponent } from './guest-list/gustList.component';
import { AdminWelcomeComponent } from './admin-welcome/adminWelcome.component';
import { HostApproveComponent } from './host-approval/hostApproval.component';
import { NgxPaginationModule } from 'ngx-pagination';
import { from } from 'rxjs';
import { HostManageComponent } from './hostmanage/hostManage.component';
import { RegisterComponent } from '../account/register.component';
import { AdminManageComponent } from './admin-manage/adminManage.component';
const ROUTES: Route[] = [
  {
    path: '',
    component: AdminComponent,
    children: [
      { path: 'block', component: BlockListComponent },
      { path: 'guest', component: GuestListComponent },
      { path: 'approve', component: HostApproveComponent },
      { path: 'host', component: HostManageComponent },
      { path: 'welcome', component: AdminWelcomeComponent },
      { path: 'register', component: RegisterComponent },
      { path: 'adminmanage', component: AdminManageComponent },
    ],
  },
];

@NgModule({
  declarations: [
    AdminComponent,
    BlockListComponent,
    GuestListComponent,
    AdminWelcomeComponent,
    HostApproveComponent,
    HostManageComponent,
    AdminManageComponent,
    RegisterComponent,
  ],
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule,
    NgxPaginationModule,
    RouterModule.forChild(ROUTES),
  ],
  schemas: [CUSTOM_ELEMENTS_SCHEMA],
})
export class AdminCheckinModule {}
